<template>
  <div class="ShipmentPrint pa-3">
    <v-row>
      <v-col cols="4">
        <!-- <h2>{{ cData.company_name }}</h2> -->
        <v-img
          :src="require('@/assets/herfa-logo-01.png')"
          aspect-ratio="3.5"
          contain
        ></v-img>
      </v-col>
      <v-col
        cols="4"
        class="text-h2 font-weight-bold d-flex justify-center align-center"
      >
        <div class="">出貨單</div>
      </v-col>
      <v-col cols="4" class="text-right">
        <h3>台南市仁德區新田二街42號之3</h3>
        <h3>TEL:(06)2794315．2794316</h3>
        <h3>FAX:(06)2794078</h3>
      </v-col>
    </v-row>

    <v-divider class="my-2"></v-divider>

    <v-row v-if="filterCustomer" class="mx-3 text-h6">
      <v-col cols="5">客戶名稱: {{ filterCustomer.name }}</v-col>
      <v-col cols="4">手機: {{ filterCustomer.cellphone }}</v-col>
      <v-col cols="3">單據號碼: {{ list.code }}</v-col>

      <v-col cols="5">單據日期: {{ list.date }}</v-col>
      <v-col cols="7" v-if="list.otheraddress"
        >指定送貨地址: {{ list.code }}</v-col
      >
      <v-col cols="7" v-else>送貨地址: {{ filterCustomer.address }}</v-col>
    </v-row>

    <v-divider class="my-2"></v-divider>

    <v-simple-table>
      <template v-slot:default>
        <thead class="text-no-wrap">
          <tr class="grey lighten-4">
            <th class="text-center text-h6 font-weight-bold">項次</th>
            <th class="text-center text-h6 font-weight-bold">
              品名 / 規格 / 施工
            </th>
            <th class="text-center text-h6 font-weight-bold">數量</th>
            <th class="text-center text-h6 font-weight-bold">公斤</th>
            <th class="text-center text-h6 font-weight-bold">單價</th>
            <th class="text-center text-h6 font-weight-bold">買賣金額</th>
            <th class="text-center text-h6 font-weight-bold">備註</th>
          </tr>
        </thead>
        <tbody class="text-center">
          <tr v-for="(item, index) in list.tempItems" :key="index">
            <td class="text-h6">{{ index + 1 }}</td>
            <td class="text-left pa-2 text-h6">
              <div class="mb-2 font-weight-bold">
                {{ item.rawmaterial.thickness }} {{ item.rawmaterial.name }} /
                <span v-if="!item.noSize"
                  >{{ item.width }} * {{ item.long }}</span
                >
              </div>
              <div
                v-if="item.punchlock"
                class="d-flex justify-space-between align-center"
              >
                <v-img
                  max-width="60px"
                  :src="
                    $store.state.base_url +
                    '/upload/punchlock/' +
                    item.punchlock.picname
                  "
                ></v-img>
                沖鎖金額:{{
                  parseFloat(item.punchlock.price) +
                  parseFloat(item.extraPunchlockPrice) * item.quantity
                }}
              </div>
              <div
                v-if="item.fold"
                class="d-flex justify-space-between align-center"
              >
                <v-img
                  max-width="60px"
                  :src="
                    $store.state.base_url + '/upload/fold/' + item.fold.picname
                  "
                ></v-img>
                折工金額:
                {{
                  (parseFloat(item.fold.price) +
                    parseFloat(item.extraFoldPrice)) *
                  item.quantity
                }}
              </div>
              <div
                v-if="item.machining"
                class="d-flex justify-space-between align-center"
              >
                <v-img
                  max-width="60px"
                  :src="
                    $store.state.base_url + '/upload/fold/' + item.fold.picname
                  "
                ></v-img>
                加工金額:
                {{
                  (parseFloat(item.machining.price) +
                    parseFloat(item.extraMachiningPrice)) *
                  item.quantity
                }}
              </div>
            </td>

            <td class="text-h6">{{ item.quantity }}</td>
            <td class="text-h6">{{ filterweight(item) }}</td>
            <td class="text-h6">
              {{
                parseFloat(item.rawmaterial.price) +
                parseFloat(item.extraRawmaterialPrice)
              }}
            </td>
            <td class="text-h6">{{ filterFinalPrice(item) }}</td>
            
            <td class="text-h6">
              {{ item.note }}
            </td>
          </tr>
        </tbody>
      </template>
    </v-simple-table>

    <v-divider class="my-2"></v-divider>

    <v-row class="text-h6 text-left font-weight-bold ma-2 grey lighten-4">
      <v-col cols="3"> 買賣金額小計: {{ priceTotal | filtermoney }} </v-col>
      <v-col cols="3">
        沖鎖金額小計: {{ optionTotal("punchlock", "extraPunchlockPrice") | filtermoney }}
      </v-col>
      <v-col cols="3">
        折工金額小計: {{ optionTotal("fold", "extraFoldPrice") | filtermoney }}
      </v-col>
      <v-col cols="3">
        加工金額小計: {{ optionTotal("machining", "extraMachiningPrice") | filtermoney }}
      </v-col>
    </v-row>
    <v-row class="text-h5 text-left font-weight-bold ma-2">
      <v-col cols="5">司機:</v-col>
      <v-col cols="3">客戶簽收:</v-col>
      <v-col cols="4" class="d-flex justify-end">
        合計金額:
        {{
          (priceTotal +
            optionTotal("punchlock", "extraPunchlockPrice") +
            optionTotal("fold", "extraFoldPrice") +
            optionTotal("machining", "extraMachiningPrice"))
            | filtermoney
        }}
        <v-chip color="red" outlined class="pa-2 mx-2" v-if="list.noDiscount"
          >現金價不折扣</v-chip
        >
      </v-col>
    </v-row>

    <div class="text-h6 font-weight-bold ma-2 mt-5">
      備註:
      貨款未付清或票據未兌現之前，本公司仍保有貨品所有權，並得不經法律程序取回
    </div>
    <!-- <pre>
      list.tempItems:{{ list.tempItems }}
    </pre> -->
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: JSON.parse(this.$route.query.data),
      cData: JSON.parse(sessionStorage.getItem("cData")),
      customerItems: [], //暫存所有客戶名單
    };
  },

  filters: {
    filtermoney(num) {
      if (!num) {
        return "";
      }
      const n = Number(num);
      return `$${n.toFixed(0).replace(/./g, (c, i, a) => {
        const currency =
          i && c !== "." && (a.length - i) % 3 === 0
            ? `, ${c}`.replace(/\s/g, "")
            : c;
        return currency;
      })}`;
    },
  },

  computed: {
    //取得本筆資料顧客資料
    filterCustomer() {
      return this.customerItems.find(
        (i) => i.snkey == this.list.customer.snkey
      );
    },
    //單價總金額
    priceTotal() {
      return this.list.tempItems.reduce((total, item) => {
        return (total += this.filterFinalPrice(item));
      }, 0);
    },
  },

  mounted() {
    // this.list = JSON.parse(this.$route.query.data);
    this.getData("customer");
  },

  methods: {
    //各種工作總金額
    optionTotal(type, extraType) {
      return this.list.tempItems.reduce((total, item) => {
        if (item[type]) {
          return (total +=
            (parseFloat(item[type].price) + parseFloat(item[extraType])) *
            item.quantity);
        } else {
          return total;
        }
      }, 0);
    },
    //重量計算
    filterweight(item) {
      if (item.noSize) {
        return "";
      }
      let val =
        item.rawmaterial.thickness *
        (item.width / 1000) *
        (item.long / 1000) *
        7.93 *
        item.quantity;

      return Math.round(val * 10) / 10; //2021.3.19 改回傳四捨五入,取小數點一位
      //無條件進位函數
      // return this.roundUp(val, 1);
    },
    //最終單價計算
    filterFinalPrice(item) {
      if (item.noSize) {
        var val =
          item.quantity *
          (parseFloat(item.rawmaterial.price) +
            parseFloat(item.extraRawmaterialPrice));
      } else {
        var val =
          item.weight *
          (parseFloat(item.rawmaterial.price) +
            parseFloat(item.extraRawmaterialPrice));
      }
      return Math.round(val); //2021.3.19 改回傳四捨五入,取整數
      // return this.roundUp(val, 0);
    },
    //取得指定資料
    getData(tableName) {
      this.myAxios(
        "general/getAll/" + this.$store.state.databaseName + "/" + tableName
      ).then((rs) => {
        // console.log(tableName,rs)
        this[tableName + "Items"] = rs.map((i) => {
          let newItem = JSON.parse(i.datalist);
          newItem.snkey = i.snkey;
          if (newItem.picname == undefined) {
            newItem.picname = "lazypic.jpg";
          }
          return newItem;
        });
      });
    },
  },
};
</script>

<style>
@media print {
  @page {
    size: A4 portrait;
    margin: 0cm;
    padding: 0cm;
  }
}
</style>